.chat-container {
	width: 100vw;
	height: 100vh;
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: center;
}

.buddy-title {
	width: 100%;
	text-align: center;
	padding: 10px 0;
	display: inline-block;
	font-size: 100%;
	background-image: linear-gradient(120deg, #d5ccfc 0%, #bcaffa 100%);
	order: 1;
}

.send-msg {
	width: 92vw;
	order: 2;
}

.send-msg > form {
	width: 100%;
	display: flex;
}

.send-msg input {
	/* width: 92%; */
	height: 30px;
	margin: 10px 0px;
	padding: 15px;
	border: 0px;
	border-radius: 10px;
	flex-grow: 1;
}

.send-msg button {
	display: inline-flex;
	align-items: center;
	margin: 10px 5px;
	height: 30px;
	padding: 15px;
	border: 0px;
	border-radius: 10px;
}

.chats {
	width: 92vw;
	height: 83vh;
	order: 2;
	overflow: visible auto;
	/* display: flex; */
}

.chat-container > button {
	order: 1;
}

.chats > .sentMsg,
.recdMsg {
	background-color: #c2e9fb;
	margin: 10px 0px;
	padding: 10px 15px;
	/* height: 30px; */
	width: 100%;
	border: 0px;
	border-radius: 5px;
	display: inline-flex;
	align-items: center;
}

.chats > .sentMsg {
	background-color: #d5ccfc;
	justify-content: flex-end;
}
