.logoutButton {
  position: absolute;
  align-self: flex-start;
  display: none;
}

.contacts-container {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: center;
  position: relative;
}

.contacts-heading {
  display: block;
  width: 100%;
  padding: 5px 0;
  text-align: center;
  background-image: linear-gradient(120deg, #d5ccfc 0%, #bcaffa 100%);
}

.contacts-list {
  list-style-type: none;
  width: 60%;
  justify-self: normal;
}

.contacts-list>li {
  width: 100%;
  padding: 10px 10px 10px 15px;
  margin: 10px 0;
  border: none;
  background-color: #d5ccfc88;
  border-radius: 10px;
  box-shadow: 1px 1px 5px #12121233;
  align-content: center;
}

.contacts-add-button {
  width: 60%;
  padding: 10px 10px 10px 15px;
  border: none;
  border-radius: 10px;
  align-content: center;
  text-align: center;
  margin-top: 5px;
  background-color: #bcaffa;
  box-shadow: 2px 2px 8px #12121233;
  align-self: center;
}

.contacts-add-button:hover, .contacts-list>li:hover {
  cursor: pointer;
  box-shadow: 2px 2px 15px #12121233;
}