* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

/*
color pallete->
#bcaffa - light purple
#d5ccfc - sub-light purple
#c2e9fb - very light blue
#f36363 - warning red
*/

body {
	background-image: linear-gradient(120deg, #bcaffa 0%, #c2e9fb 100%);
}

.container {
	width: 100vw;
	height: 100vh;
	display: flex;
	flex-direction: column;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
}

.inputField {
	margin: 5px;
	padding: 10px;
	border-radius: 8px;
	border: 5px;
	height: 30px;
	width: 50%;
	background-color: #eeeeff;
	color: #121212;
}

.loginButton {
	width: 50%;
	height: 30px;
	border: 3px;
	border-radius: 8px;
	margin-top: 10px;
	background-color: #c2e9fb;
	opacity: 0.8;
	box-shadow: 1px 1px 5px #12121233;
}

form {
	text-align: center;
}

@media (min-width: 720px) {
	form {
		width: 500px;
	}
}

footer {
	display: block;
	width: 100%;
	text-align: center;
	position: absolute;
	top: 95vh;
	font-size: 12pt;
	opacity: 50%;
}

.login-fail {
	width: 100%;
	text-align: center;
	position: absolute;
	top: 70vh;
	margin: 15px 0;
	color: #f36363;
	font-size: 8pt;
}

.login-fail-hidden {
	display: none;
}

